import React, { useEffect, useState } from "react";
import AWS from "aws-sdk";
import { useDropzone } from "react-dropzone";
import { FiUploadCloud } from "react-icons/fi";

const ID = "AKIASULQMX62UGAJO5VI";
const SECRET = "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";
const S3_BUCKET = "indraniapp";
const REGION = "us-east-2";

AWS.config.update({
  accessKeyId: ID,
  secretAccessKey: SECRET,
});
const myBucket = new AWS.S3({
  params: { Bucket: S3_BUCKET },
  region: REGION,
});
const Uploader = ({ setImageUrl, imageUrl }) => {
  const [progress, setProgress] = useState(0);
  useEffect(() => {
    console.log(progress);
  }, [progress]);

  const [files, setFiles] = useState([]);

  const { getRootProps, getInputProps } = useDropzone({
    accept: "image/*",
    multiple: true,
    maxSize: 500000,
    onDrop: (acceptedFiles) => {
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
  });

  useEffect(() => {
    console.log("uploader file has been changed", files);
    if (files) {
      files.forEach((file) => {
        const params = {
          ACL: "public-read",
          Body: file,
          Bucket: S3_BUCKET,
          Key: file.name,
        };
        console.log("upload start");
        myBucket
          .upload(params)
          .on("httpUploadProgress", (evt, data) => {
            console.log(evt, data);
            setProgress(Math.round((evt.loaded / evt.total) * 100));
          })
          .send((err, data) => {
            if (err) {
              console.log(err);
              return;
            }
            if (Array.isArray(imageUrl)) {
              setImageUrl((prevState) => [
                ...prevState,
                { path: data.Location, name: data.key },
              ]);
              console.log(data);
            } else {
              setImageUrl([{ path: data.Location, name: data.key }]);
            }
          });
        // const formData = new FormData();
        // formData.append('img', postFiles);
        // axios({
        //   url: uploadURL,
        //   method: 'POST',
        //   data: formData,
        // })
        //   .then((res) => {
        //     console.log(res.data)
        //     // setImageUrl(`${process.env.REACT_APP_API_BASE_URL}/images/${res.data.images[0]}`);
        //   })
        //   .catch((err) => console.log(err));
      });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [files]);

  const thumbs = files.map((file) => (
    <div key={file.name}>
      <div>
        <img
          className="inline-flex border-2 border-gray-100 w-24 max-h-24"
          src={file.preview}
          alt={file.name}
        />
      </div>
    </div>
  ));

  useEffect(
    () => () => {
      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach((file) => URL.revokeObjectURL(file.preview));
    },
    [files]
  );

  return (
    <div className="w-full text-center">
      <div
        className="px-6 pt-5 pb-6 border-2 border-gray-300 dark:border-gray-600 border-dashed rounded-md cursor-pointer"
        {...getRootProps()}
      >
        <input {...getInputProps()} />
        <span className="mx-auto flex justify-center">
          <FiUploadCloud className="text-3xl text-green-500" />
        </span>
        <p className="text-sm mt-2">Drag your image here</p>
        <em className="text-xs text-gray-400">
          (Only *.jpeg and *.png images will be accepted)
        </em>
      </div>
      <aside className="flex flex-row flex-wrap mt-4">
        {imageUrl?.length > 0
          ? imageUrl.map((ele, idx) => (
              <img
                key={idx}
                className="inline-flex border rounded-md border-gray-100 dark:border-gray-600 w-24 max-h-24 p-2"
                src={ele.path}
                alt={ele.name}
              />
            ))
          : thumbs}
      </aside>
    </div>
  );
};

export default Uploader;
