import { useContext, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import Cookies from 'js-cookie';
import { useLocation } from 'react-router';

import AdminServices from '../services/AdminServices';
import { AdminContext } from '../context/AdminContext';
import { SidebarContext } from '../context/SidebarContext';
import { notifyError, notifySuccess } from '../utils/toast';

const useStaffSubmit = (id) => {
  const { state } = useContext(AdminContext);
  const { adminInfo } = state;
  const [imageUrl, setImageUrl] = useState([]);
  const [adminRoles, setAdminRoles] = useState({
    productread:false,
    productwrite:false,
    categoryread:false,
    categorywrite:false,
    customerread:false,
    customerwrite:false,
    orderread:false,
    orderwrite:false,
    cuoponread:false,
    cuoponwrite:false,
    staffsread:false,
    staffswrite:false,
    inventoryread:false,
    inventorywrite:false,
    enquiryread:false,
    enquirywrite:false,
    notificationread:false,
    notificationwrite:false,
  });
  const location = useLocation();
  const { isDrawerOpen, closeDrawer, setIsUpdate } = useContext(SidebarContext);

  const {
    register,
    handleSubmit,
    setValue,
    clearErrors,
    formState: { errors },
  } = useForm();

  const handleChecked = (e) => {
    setAdminRoles(prevState => ({
      ...prevState,
      [e.target.name]:e.target.checked
    }));
  }

  const roles = [
    {
      name: "product",
      read: "productread",
      write: "productwrite",
      readvalue: adminRoles.productread,
      writevalue: adminRoles.productwrite,
    },
    {
      name: "category",
      read: "categoryread",
      write: "categorywrite",
      readvalue: adminRoles.categoryread,
      writevalue: adminRoles.categorywrite
    },
    {
      name: "customer",
      read: "customerread",
      write: "customerwrite",
      readvalue: adminRoles.customerread,
      writevalue: adminRoles.customerwrite,
    },
    {
      name: "order",
      read: "orderread",
      write: "orderwrite",
      readvalue: adminRoles.orderread,
      writevalue: adminRoles.orderwrite, 
    },
    {
      name: "cuopon",
      read: "cuoponread",
      write: "cuoponwrite",
      readvalue: adminRoles.cuoponread,
      writevalue: adminRoles.cuoponwrite,
    },
    {
      name: "staffs",
      read: "staffsread",
      write: "staffswrite",
      readvalue: adminRoles.staffsread,
      writevalue: adminRoles.staffswrite,
    },
    {
      name: "inventory",
      read: "inventoryread",
      write: "inventorywrite",
      readvalue: adminRoles.inventoryread,
      writevalue: adminRoles.inventorywrite,
    },
    {
      name: "enquiry",
      read: "enquiryread",
      write: "enquirywrite",
      readvalue: adminRoles.enquiryread,
      writevalue: adminRoles.enquirywrite,
    },
    {
      name: "notification",
      read: "notificationread",
      write: "notificationwrite",
      readvalue: adminRoles.notificationread,
      writevalue: adminRoles.notificationwrite,
    },
  ]

  const onSubmit = (data) => {
    if (!imageUrl) {
      notifyError('Image is required!');
      return;
    }
    const staffData = {
      fullname: data.fullname,
      email: data.email,
      password: data.password,
      phoneno: data.phoneno,
      joiningDate: data.joiningDate,
      jobrole: data.jobrole,
      image: imageUrl[0].path,
      roles: {
          products: {
            read: adminRoles.productread,
            write: adminRoles.productwrite,
          },
          category: {
            read: adminRoles.categoryread,
            write: adminRoles.categoryread,
          },
          customer: {
            read: adminRoles.customerread,
            write: adminRoles.cuoponwrite,
          },
          order: {
            read: adminRoles.orderread,
            write: adminRoles.orderwrite,
          },
          cuopon: {
            read: adminRoles.cuoponread,
            write: adminRoles.cuoponwrite,
          },
          staffs: {
            read: adminRoles.staffsread,
            write: adminRoles.staffswrite,
          },
          inventory: {
            read: adminRoles.inventoryread,
            write: adminRoles.inventorywrite,
          },
          enquiry: {
            read: adminRoles.enquiryread,
            write: adminRoles.enquirywrite,
          },
          notification: {
            read: adminRoles.notificationread,
            write: adminRoles.notificationwrite,
          },
      }
    };

    if (id) {
      console.log(staffData)
      AdminServices.updateStaff(id, staffData)
        .then((res) => {
          console.log(res);
          setIsUpdate(true);
          notifySuccess('Staff Updated Successfully!');
          closeDrawer();
        })
        .catch((err) => {
          notifyError(err.message);
        });
      
    } else {
      AdminServices.registerAdmin(staffData)
        .then((res) => {
          setIsUpdate(true);
          notifySuccess(res.message);
          closeDrawer();
        })
        .catch((err) => notifyError(err.message));
    }
  };

  useEffect(() => {
    if (!isDrawerOpen) {
      setValue('fullname');
      setValue('email');
      setValue('password');
      setValue('phoneno');
      setValue('joiningDate');
      setValue('jobrole');
      setImageUrl('');
      clearErrors('fullname');
      clearErrors('email');
      clearErrors('password');
      clearErrors('phoneno');
      clearErrors('joiningDate');
      clearErrors('jobrole');
      return;
    }
    if (id) {
      AdminServices.getStaffById(id, { email: adminInfo.email })
        .then((res) => {
          console.log(res)
          if (res) {
            setValue('fullname', res.fullname);
            setValue('email', res.email);
            setValue('password', res.password);
            setValue('phoneno', res.phoneno);
            setValue('joiningDate', new Date(res.created_at));
            setValue('jobrole', res.jobrole);
            setImageUrl([{ path: res.image, name: res.fullname }]);
            setAdminRoles({
              productread: res.roles.products.read,
              productwrite: res.roles.products.write,
              categoryread: res.roles.category.read,
              categorywrite: res.roles.category.write,
              customerread: res.roles.customer.read,
              customerwrite: res.roles.customer.write,
              orderread: res.roles.order.read,
              orderwrite: res.roles.order.write,
              cuoponread: res.roles.cuopon.read,
              cuoponwrite: res.roles.cuopon.write,
              staffsread: res.roles.staffs.read,
              staffswrite: res.roles.staffs.write,
              inventoryread: res.roles.inventory.read,
              inventorywrite: res.roles.inventory.write,
              enquiryread: res.roles.enquiry.read,
              enquirywrite: res.roles.enquiry.write,
              notificationread: res.roles.notification.read,
              notificationwrite: res.roles.notification.write,
            })
          }
        })
        .catch((err) => {
          notifyError('There is a server error!');
        });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, setValue, isDrawerOpen, adminInfo.email]);

  useEffect(() => {
    if (
      location.pathname === '/setting' ||
      (location.pathname === '/edit-profile' && Cookies.get('adminInfo'))
    ) {
      const user = JSON.parse(Cookies.get('adminInfo'));
      setValue('fullname', user.fullname);
      setValue('email', user.email);
      setValue('phoneno', user.phoneno);
      setValue('jobrole', user.role);
      setImageUrl([{ path: user.image, name: user.fullname }]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return {
    register,
    handleSubmit,
    onSubmit,
    errors,
    setImageUrl,
    imageUrl,
    adminRoles,
    setAdminRoles,
    handleChecked,
    roles,
  };
};

export default useStaffSubmit;
