import React from 'react';
import Scrollbars from 'react-custom-scrollbars';
import { Card, CardBody, Input } from '@windmill/react-ui';

import Title from '../form/Title';
import Error from '../form/Error';
import LabelArea from '../form/LabelArea';
import InputArea from '../form/InputArea';
import SelectRole from '../form/SelectRole';
import DrawerButton from '../form/DrawerButton';
import Uploader from '../image-uploader/Uploader';
import useStaffSubmit from '../../hooks/useStaffSubmit';

const StaffDrawer = ({ id }) => {
  const {
    register,
    handleSubmit,
    onSubmit,
    errors,
    imageUrl,
    setImageUrl,
    handleChecked,
    roles,
  } = useStaffSubmit(id);

  return (
    <>
      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {id ? (
          <Title
            title="Update Staff"
            description="Updated your staff necessary information from here"
          />
        ) : (
          <Title
            title="Add Staff"
            description="Add your staff necessary information from here"
          />
        )}
      </div>
      <Scrollbars className="w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <Card className="overflow-y-scroll flex-grow scrollbar-hide w-full max-h-full">
          <CardBody>
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="px-6 pt-8 flex-grow scrollbar-hide w-full max-h-full pb-40">
                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Staff Image" />
                  <div className="col-span-8 sm:col-span-4">
                    <Uploader imageUrl={imageUrl} setImageUrl={setImageUrl} />
                  </div>
                </div>

                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Name" />
                  <div className="col-span-8 sm:col-span-4">
                    <InputArea
                      register={register}
                      label="Name"
                      name="fullname"
                      type="text"
                      placeholder="Staff name"
                    />
                    <Error errorName={errors.fullname} />
                  </div>
                </div>

                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Email" />
                  <div className="col-span-8 sm:col-span-4">
                    <InputArea
                      register={register}
                      label="Email"
                      name="email"
                      type="text"
                      placeholder="Email"
                    />
                    <Error errorName={errors.email} />
                  </div>
                </div>

                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Password" />
                  <div className="col-span-8 sm:col-span-4">
                    <InputArea
                      register={register}
                      label="Password"
                      name="password"
                      type="password"
                      placeholder="Password"
                    />
                    <Error errorName={errors.password} />
                  </div>
                </div>

                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Contact Number" />
                  <div className="col-span-8 sm:col-span-4">
                    <InputArea
                      register={register}
                      label="Contact Number"
                      name="phoneno"
                      type="text"
                      placeholder="Phone number"
                    />
                    <Error errorName={errors.phoneno} />
                  </div>
                </div>

                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Joining Date" />
                  <div className="col-span-8 sm:col-span-4">
                    <InputArea
                      register={register}
                      label="Joining Date"
                      name="joiningDate"
                      type="datetime-local"
                      placeholder="Joining date"
                    />
                    <Error errorName={errors.joiningDate} />
                  </div>
                </div>

                <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label="Staff Role" />
                  <div className="col-span-8 sm:col-span-4">
                    <SelectRole register={register} label="Role" name="jobrole" />
                    <Error errorName={errors.jobrole} />
                  </div>
                </div>
                

                {roles.map((ele, idx) => <div key={idx} className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                  <LabelArea label={ele.name} />
                  <div className="col-span-8 sm:col-span-4">
                    <span>read</span>
                    <span className="mx-1"></span>
                    <Input
                      onChange={(e)=>handleChecked(e)}
                      name={ele.read}
                      type="checkbox"
                      checked={ele.readvalue}
                    />
                    <span className="mx-3"></span>
                    <span>write</span>
                    <span className="mx-1"></span>
                    <Input
                      onChange={(e) => handleChecked(e)}
                      name={ele.write}
                      type="checkbox"
                      checked={ele.writevalue}
                    />
                  </div>
                </div>)}
              </div>

              <DrawerButton id={id} title="Staff" />
            </form>
          </CardBody>
        </Card>
      </Scrollbars>
    </>
  );
};

export default StaffDrawer;
